'use client';

import Calendar from 'react-calendar';
import 'react-calendar/dist/Calendar.css';
import '@/app/styles/globals.css';
import { useState } from 'react';
import { addGolfOuting } from '@/utils/supabase'; // Make sure path is correct

function DatePicker() {
    const [showForm, setShowForm] = useState(false);
    const [date, setDate] = useState(new Date());
    const [courseName, setCourseName] = useState('');
    const [score, setScore] = useState('');
    const [coursePar, setCoursePar] = useState('');

    const handleDateChange = (selectedDate) => {
        setDate(selectedDate);
    };

    const handleSubmit = async () => {
        if (!courseName || !score || !coursePar) {
            alert('Please fill out all fields');
            return;
        }

        await addGolfOuting({
            played_on: date,
            course_name: courseName,
            score: parseInt(score, 10),
            course_par: parseInt(coursePar, 10),
        });

        // Reset fields
        setCourseName('');
        setScore('');
        setCoursePar('');
        setShowForm(false);
    };

    return (
        <div className="relative text-center">
            <button
                className="cool-golf-button"
                onClick={() => setShowForm(!showForm)}
            >
                Add Golf Outing
            </button>

            {showForm && (
                <div className="absolute top-14 left-1/2 transform -translate-x-1/2 z-50 bg-white rounded-xl shadow-xl p-6 w-80 space-y-4">
                    <Calendar onChange={handleDateChange} value={date} />

                    <input
                        type="text"
                        placeholder="Course Name"
                        value={courseName}
                        onChange={(e) => setCourseName(e.target.value)}
                        className="w-full border rounded p-2"
                    />

                    <input
                        type="number"
                        placeholder="Score"
                        value={score}
                        onChange={(e) => setScore(e.target.value)}
                        className="w-full border rounded p-2"
                    />

                    <input
                        type="number"
                        placeholder="Course Par"
                        value={coursePar}
                        onChange={(e) => setCoursePar(e.target.value)}
                        className="w-full border rounded p-2"
                    />

                    <button
                        className="bg-green-600 text-white w-full py-2 rounded hover:bg-green-700"
                        onClick={handleSubmit}
                    >
                        Submit
                    </button>
                </div>
            )}
        </div>
    );
}

export default DatePicker;
